import React, { Component } from 'react';
import { Row, Col, Form, Select, Radio, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { setMajorsName, setSemesterRegister } from 'dump/data';
import { BtnSearch } from 'components/common/button';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommonCreateNewClass extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    const RadioGroup = Radio.Group;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={6}>
            <Form.Item label="Ngnh:">
              {getFieldDecorator('setMajorsName', {})(
                <Select>
                  {setMajorsName.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="K ng k:">
              {getFieldDecorator('setSemesterRegister', {})(
                <Select>
                  {setSemesterRegister.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="K hiu, tn mn:">
              <Input />
            </Form.Item>
          </Col>
        </Row>
        <br />
        <Row gutter={24}>
          <Col span={24}>
            <Form.Item>
              {getFieldDecorator('conCerti', {})(
                <div>
                  <RadioGroup onChange={this.onChange} value={this.state.value}>
                    <Radio value={1}>Tt c mn</Radio>
                    <Radio value={2}>Mn chung</Radio>
                    <Radio value={3}>Mn khoa</Radio>
                  </RadioGroup>
                  <BtnSearch />
                </div>
              )}
            </Form.Item>
          </Col>
        </Row>
        <br />
      </Form>
    );
  }
}

export default FormInfoCommonCreateNewClass;



