import React, { Component } from 'react';
import { Table, Select, Input, InputNumber, Button, Icon } from 'antd';
import {
  setTablePracticeClass,
  setTablePracticeShiftSchool,
  setTablePracticeClassRoom,
  setTablePracticeTeacher,
} from 'dump/data';
import './style.scss';

class TableListNewClass extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    const { Option } = Select;

    this.columns = [
      {
        title: 'Lp TC',
        dataIndex: 'lopTC',
        key: 'lopTC',
      },
      {
        title: 'Ti thiu',
        dataIndex: 'toi-thieu',
        render: () => <Input style={{ width: '100%' }} />,
      },
      {
        title: 'Ti a',
        dataIndex: 'toi-da',
        render: () => <Input style={{ width: '100%' }} />,
      },
      {
        title: 'S SV ng k',
        dataIndex: 'soSVDangKy',
        key: 'soSVDangKy',
      },
      {
        title: 'S tit tun',
        dataIndex: 'soTietTuan',
        key: 'soTietTuan',
      },
      {
        title: 'Th',
        dataIndex: 'thu',
        render: () => <InputNumber min={1} max={8} defaultValue={1} style={{ width: '100%' }} />,
      },
      {
        title: 'T tit',
        dataIndex: 'tu-tiet',
        render: () => <InputNumber min={1} max={14} defaultValue={1} style={{ width: '100%' }} />,
      },
      {
        title: 'n tit',
        dataIndex: 'den-tiet',
        render: () => <InputNumber min={1} max={14} defaultValue={1} style={{ width: '100%' }} />,
      },
      {
        title: 'Ca hc',
        dataIndex: 'ca-hoc',
        render: () =>
          setTablePracticeShiftSchool.length >= 1 ? (
            <Select style={{ width: '100%' }}>
              {setTablePracticeShiftSchool.map(item => (
                <Option value={item.code}>{item.name}</Option>
              ))}
            </Select>
          ) : null,
      },
      {
        title: 'Phng hc',
        dataIndex: 'phong-hoc',
        render: () =>
          setTablePracticeClassRoom.length >= 1 ? (
            <Select style={{ width: '100%' }}>
              {setTablePracticeClassRoom.map(item => (
                <Option value={item.code}>{item.name}</Option>
              ))}
            </Select>
          ) : null,
      },
      {
        title: 'Gio vin',
        dataIndex: 'giao-vien',
        render: () =>
          setTablePracticeTeacher.length >= 1 ? (
            <Select style={{ width: '100%' }}>
              {setTablePracticeTeacher.map(item => (
                <Option value={item.code}>{item.name}</Option>
              ))}
            </Select>
          ) : null,
      },
      {
        title: 'T tun',
        dataIndex: 'tuTuan',
        key: 'tuTuan',
      },
      {
        title: 'n tun',
        dataIndex: 'denTuan',
        key: 'denTuan',
      },
      {
        title: 'T ngy',
        dataIndex: 'tuNgay',
        key: 'tuNgay',
      },
      {
        title: 'n ngy',
        dataIndex: 'denNgay',
        key: 'denNgay',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <div style={{ textAlign: 'right' }}>
          <Button type="primary" className="btnSetDecisionNumber">
            <Icon type="export" /> Thm nhm
          </Button>
        </div>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setTablePracticeClass}
          columns={this.columns}
          scroll={{ x: 'true' }}
        />
      </div>
    );
  }
}

export default TableListNewClass;



