import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Divider } from 'antd';
// import MenuTabTop from './MenuTabTop';
import FormInfoCommon from './FormInfoCommon.js';
import TableListCreditSubject from './TableListCreditSubject';
import TableListCreditClass from './TableListCreditClass';
import TableListNewClass from './TableListNewClass';
import './style.scss';

class CreatePracticeSchedulePage extends Component {
  state = {};
  render() {
    return (
      <Card title="To TKB thc hnh">
        <Row>
          <Col span={24}>
            <FormInfoCommon />
          </Col>
        </Row>
        <Divider>Danh sch mn TC</Divider>
        <Row>
          <Col span={24}>
            <TableListCreditSubject />
          </Col>
        </Row>
        <Divider>Thng tin lp TC</Divider>
        <Row>
          <Col span={24}>
            <TableListCreditClass />
          </Col>
        </Row>
        <Divider>Thm nhm cho lp TC</Divider>
        <Row>
          <Col span={24}>
            <TableListNewClass />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <CreatePracticeSchedulePage />
  </Page>
);



