import React, { Component } from 'react';
import { Table } from 'antd';
import { setTablePracticeSubject } from 'dump/data';
import './style.scss';

class TableListCreditSubject extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'K hiu tn lp TC',
        dataIndex: 'kyHieu',
        key: 'kyHieu',
      },
      {
        title: 'Tn mn',
        dataIndex: 'tenMon',
        key: 'tenMon',
      },
      {
        title: 'S TC',
        dataIndex: 'soTC',
        key: 'soTC',
      },
      {
        title: 'H s',
        dataIndex: 'heSo',
        key: 'heSo',
      },
      {
        title: 'S lp',
        dataIndex: 'soLop',
        key: 'soLop',
      },
      {
        title: 'S SV ti thiu',
        dataIndex: 'soSVToiThieu',
        key: 'soSVToiThieu',
      },
      {
        title: 'S SV ti a',
        dataIndex: 'soSVToiDa',
        key: 'soSVToiDa',
      },
      {
        title: 'S SV ng k',
        dataIndex: 'soSVDangKy',
        key: 'soSVDangKy',
      },
      {
        title: 'Tng s tit',
        dataIndex: 'tongSoTiet',
        key: 'tongSoTiet',
      },
      {
        title: 'S tit LT',
        dataIndex: 'soTietLT',
        key: 'soTietLT',
      },
      {
        title: 'S tit TH',
        dataIndex: 'soTietTH',
        key: 'soTietTH',
      },
      {
        title: 'S tit BT',
        dataIndex: 'soTietBT',
        key: 'soTietBT',
      },
      {
        title: 'S tit TL',
        dataIndex: 'soTietTL',
        key: 'soTietTL',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <Table
        bordered
        rowSelection={this.rowSelection}
        dataSource={setTablePracticeSubject}
        columns={this.columns}
        scroll={{ x: 'true' }}
      />
    );
  }
}

export default TableListCreditSubject;



